.item {
  display: flex;
  flex-direction: row;
  background-color: white;
  box-shadow: 0px 0px 7px 0px rgb(121, 122, 125);
  border-radius: 4px;
  margin-bottom: 20px;
  width: 200px;
  padding: 20px;
  height: 40px;
  gap: 20px;
  align-items: center;
}
.avatar {
  height: 48px;
  border-radius: 5px;
  background-color: rgba(121, 122, 125, 0.267);
}
.status {
  width: 10px;
  height: 10px;
  border-radius: 50%;
}
.online {
  background-color: green;
}
.offline {
  background-color: red;
}
.name {
  font-size: 20px;
  font-weight: 600;
}
